
.ui-tab-header-item{-webkit-box-align:center;-ms-flex-align:center;align-items:center;cursor:pointer;display:-webkit-box;display:-ms-flexbox;display:flex;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";height:3rem;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;min-width:5rem;padding:0 .75rem;position:relative;text-transform:uppercase
}
.ui-tab-header-item:hover{background-color:rgba(0,0,0,0.05)
}
.ui-tab-header-item.is-disabled{background-color:transparent;cursor:default;opacity:0.4;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.ui-tab-header-item--type-icon-and-text{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;height:4.5rem
}
.ui-tab-header-item--type-icon-and-text .ui-tab-header-item__icon{margin-bottom:.25rem
}
.ui-tab-header-item__text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-size:.9375rem;font-weight:500
}
.ui-tab-header-item__icon{height:1.5rem;width:1.5rem
}
.ui-tab-header-item__icon .ui-icon{cursor:inherit
}



