
.ui-checkbox{-webkit-box-align:center;-ms-flex-align:center;align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-weight:normal;margin:0;margin-bottom:.5rem;position:relative
}
.ui-checkbox:not(.is-disabled):not(.is-checked):hover .ui-checkbox__checkmark::before,.ui-checkbox:not(.is-disabled):not(.is-checked).is-active .ui-checkbox__checkmark::before{border-color:rgba(0,0,0,0.6)
}
.ui-checkbox.is-checked .ui-checkbox__checkmark::after{border-bottom:.125rem solid white;border-right:.125rem solid white;opacity:1
}
.ui-checkbox.is-disabled .ui-checkbox__checkmark,.ui-checkbox.is-disabled .ui-checkbox__label-text{color:rgba(0,0,0,0.38);cursor:default
}
.ui-checkbox.is-disabled .ui-checkbox__checkmark::before{border-color:rgba(0,0,0,0.26)
}
.ui-checkbox.is-disabled.is-checked .ui-checkbox__checkmark::before{background-color:rgba(0,0,0,0.26);border:none
}
.ui-checkbox__label-text{cursor:pointer;font-size:1rem;margin-left:.5rem
}
.ui-checkbox__checkmark{background-color:white;cursor:pointer;-ms-flex-negative:0;flex-shrink:0;height:1.25rem;position:relative;width:1.25rem
}
.ui-checkbox__checkmark::before{border-radius:.125rem;border:.125rem solid rgba(0,0,0,0.38);-webkit-box-sizing:border-box;box-sizing:border-box;content:"";display:block;height:100%;left:0;position:absolute;top:0;-webkit-transition:all 0.3s ease;transition:all 0.3s ease;width:100%
}
.ui-checkbox__checkmark::after{bottom:.3125rem;-webkit-box-sizing:border-box;box-sizing:border-box;content:"";display:block;height:.8125rem;left:.4375rem;opacity:0;position:absolute;-webkit-transform:rotate(45deg);transform:rotate(45deg);-webkit-transition-delay:0.1s;transition-delay:0.1s;-webkit-transition:opacity 0.3s ease;transition:opacity 0.3s ease;width:.375rem
}
.ui-checkbox__input{position:absolute;opacity:0
}
body[modality="keyboard"] .ui-checkbox__input:focus+.ui-checkbox__checkmark .ui-checkbox__focus-ring{opacity:1;-webkit-transform:scale(1);transform:scale(1)
}
.ui-checkbox__focus-ring{border-radius:50%;height:2.625rem;margin-left:-.6875rem;margin-top:-.6875rem;opacity:0;position:absolute;-webkit-transform:scale(0);transform:scale(0);-webkit-transition-duration:.15s;transition-duration:.15s;-webkit-transition-property:opacity, -webkit-transform;transition-property:opacity, -webkit-transform;transition-property:opacity, transform;transition-property:opacity, transform, -webkit-transform;-webkit-transition-timing-function:ease-out;transition-timing-function:ease-out;width:2.625rem;background-color:rgba(0,0,0,0.12)
}
.ui-checkbox--box-position-right .ui-checkbox__label-text{margin-left:0;margin-right:auto;-webkit-box-ordinal-group:0;-ms-flex-order:-1;order:-1
}
.ui-checkbox--color-primary:not(.is-disabled).is-checked:hover .ui-checkbox__checkmark::before,.ui-checkbox--color-primary:not(.is-disabled).is-checked.is-active .ui-checkbox__checkmark::before{background-color:#0d8aee;border-color:#0d8aee
}
.ui-checkbox--color-primary.is-checked .ui-checkbox__checkmark::before{background-color:#2196f3;border-color:#2196f3
}
.ui-checkbox--color-primary.is-checked .ui-checkbox__focus-ring{background-color:rgba(33,150,243,0.18)
}
.ui-checkbox--color-accent:not(.is-disabled).is-checked:hover .ui-checkbox__checkmark::before,.ui-checkbox--color-accent:not(.is-disabled).is-checked.is-active .ui-checkbox__checkmark::before{background-color:#bf00e0;border-color:#bf00e0
}
.ui-checkbox--color-accent.is-checked .ui-checkbox__checkmark::before{background-color:#d500f9;border-color:#d500f9
}
.ui-checkbox--color-accent.is-checked .ui-checkbox__focus-ring{background-color:rgba(213,0,249,0.18)
}



