
.sentiment-info .ui-modal--size-large .ui-modal__container{width:65.5rem
}
.sentiment-info .modal .body{position:relative;overflow:hidden
}
.sentiment-info .modal .body svg text{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.sentiment-info .modal .body svg text::-moz-selection{background:none
}
.sentiment-info .modal .body svg text::selection{background:none
}
.sentiment-info .modal .body svg .plot-info .caption{font-size:18px;text-anchor:middle;alignment-baseline:middle
}
.sentiment-info .modal .body svg .plot-info .title{font-size:30px
}
.sentiment-info .modal .body svg .plot-info .legend{text-anchor:middle;alignment-baseline:hanging
}
.sentiment-info .modal .body svg .plot .sentiment-nodes-enter-active,.sentiment-info .modal .body svg .plot .sentiment-nodes-leave-active{-webkit-transition:all 1s;transition:all 1s
}
.sentiment-info .modal .body svg .plot .sentiment-nodes-enter,.sentiment-info .modal .body svg .plot .sentiment-nodes-leave-to{opacity:0
}
.sentiment-info .modal .body svg .plot .node{stroke:#aaa;stroke-width:1px
}
.sentiment-info .modal .body svg .plot .node:hover{stroke:orange;stroke-width:3px
}
.sentiment-info .modal .body svg .plot .axis line,.sentiment-info .modal .body svg .plot .axis path{fill:none;stroke:grey;stroke-width:2
}
.sentiment-info .modal .body svg .plot .axis text{font-family:sans-serif;font-size:11px
}
.sentiment-info .modal .body .tooltip{position:absolute;border-radius:10px;-webkit-box-shadow:4px 4px 10px rgba(0,0,0,0.4);box-shadow:4px 4px 10px rgba(0,0,0,0.4);pointer-events:none;width:300px;height:auto;padding:10px;background-color:#fff
}



