
.ui-ripple-ink{border-radius:inherit;bottom:0;display:block;left:0;overflow:hidden;position:absolute;right:0;top:0;-webkit-mask-image:-webkit-radial-gradient(circle, white, black)
}
.ui-ripple-ink__ink{background-clip:padding-box;background-color:currentColor;border-radius:50%;height:0;opacity:0.2;pointer-events:none;position:absolute;-webkit-transform:scale(0);transform:scale(0);-webkit-transition:opacity 0.6s ease-out, -webkit-transform 0.6s ease-out;transition:opacity 0.6s ease-out, -webkit-transform 0.6s ease-out;transition:transform 0.6s ease-out, opacity 0.6s ease-out;transition:transform 0.6s ease-out, opacity 0.6s ease-out, -webkit-transform 0.6s ease-out;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:0
}
.ui-ripple-ink__ink.is-held{opacity:0.4;-webkit-transform:scale(1);transform:scale(1)
}
.ui-ripple-ink__ink.is-done{opacity:0 !important
}



