
.ui-snackbar-container{bottom:0;left:.5rem;overflow:hidden;position:absolute
}
.ui-snackbar-container .ui-snackbar{margin:.25rem .25rem .75rem .25rem
}
.ui-snackbar-container--position-right{left:initial;right:.5rem
}
.ui-snackbar-container--position-center{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;left:.5rem;right:.5rem
}



