
.ui-calendar-month{height:16rem;overflow:hidden;position:relative;width:100%
}
.ui-calendar-month__header{-webkit-box-align:center;-ms-flex-align:center;align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex;height:2.5rem;-ms-flex-pack:distribute;justify-content:space-around;width:100%
}
.ui-calendar-month__header span{-webkit-box-align:center;-ms-flex-align:center;align-items:center;color:rgba(0,0,0,0.54);display:-webkit-box;display:-ms-flexbox;display:flex;font-size:.875rem;height:2.25rem;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;text-transform:uppercase;width:2.25rem
}
.ui-calendar-month__week{position:absolute;width:15.75rem
}
.ui-calendar-month__week.is-current{left:0
}
.ui-calendar-month__week.is-other{left:15.75rem
}
.ui-calendar-month__week.is-other.ui-calendar-month--slide-right{left:-15.75rem
}
.ui-calendar-month__week.is-sliding{-webkit-transition:-webkit-transform 250ms ease;transition:-webkit-transform 250ms ease;transition:transform 250ms ease;transition:transform 250ms ease, -webkit-transform 250ms ease
}
.ui-calendar-month__week.is-sliding.ui-calendar-month--slide-left{-webkit-transform:translate3d(-15.75rem, 0, 0);transform:translate3d(-15.75rem, 0, 0)
}
.ui-calendar-month__week.is-sliding.ui-calendar-month--slide-right{-webkit-transform:translate3d(15.75rem, 0, 0);transform:translate3d(15.75rem, 0, 0)
}



