
.ui-progress-circular{position:relative
}
.ui-progress-circular__determinate{-webkit-transform:rotate(270deg);transform:rotate(270deg)
}
.ui-progress-circular__determinate-path{stroke-dashoffset:0;-webkit-transition:stroke-dashoffset .3s;transition:stroke-dashoffset .3s
}
.ui-progress-circular__indeterminate{-webkit-animation:ui-progress-circular-rotate .7s linear infinite;animation:ui-progress-circular-rotate .7s linear infinite;bottom:0;height:100%;left:0;margin:auto;position:absolute;right:0;top:0;-webkit-transform-origin:center center;transform-origin:center center;width:100%
}
.ui-progress-circular__indeterminate-path{stroke-dasharray:89,200;stroke-dashoffset:-35px;stroke-linecap:round
}
.ui-progress-circular--color-multi-color .ui-progress-circular__determinate-path{stroke:#2196f3
}
.ui-progress-circular--color-multi-color .ui-progress-circular__indeterminate-path{-webkit-animation:ui-progress-circular-color 6s ease-in-out infinite;animation:ui-progress-circular-color 6s ease-in-out infinite
}
.ui-progress-circular--color-primary .ui-progress-circular__determinate-path,.ui-progress-circular--color-primary .ui-progress-circular__indeterminate-path{stroke:#2196f3
}
.ui-progress-circular--color-accent .ui-progress-circular__determinate-path,.ui-progress-circular--color-accent .ui-progress-circular__indeterminate-path{stroke:#d500f9
}
.ui-progress-circular--color-black .ui-progress-circular__determinate-path,.ui-progress-circular--color-black .ui-progress-circular__indeterminate-path{stroke:#212121
}
.ui-progress-circular--color-white .ui-progress-circular__determinate-path,.ui-progress-circular--color-white .ui-progress-circular__indeterminate-path{stroke:white
}
.ui-progress-circular--transition-fade-enter-active,.ui-progress-circular--transition-fade-leave-active{-webkit-transition:opacity 0.3s ease, -webkit-transform 0.3s ease;transition:opacity 0.3s ease, -webkit-transform 0.3s ease;transition:opacity 0.3s ease, transform 0.3s ease;transition:opacity 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease
}
.ui-progress-circular--transition-fade-enter,.ui-progress-circular--transition-fade-leave-active{opacity:0;-webkit-transform:scale(0);transform:scale(0)
}
@-webkit-keyframes ui-progress-circular-rotate{
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}
@keyframes ui-progress-circular-rotate{
100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)
}
}
@-webkit-keyframes ui-progress-circular-color{
0%,100%{stroke:#f44336
}
40%{stroke:#2196f3
}
66%{stroke:#4caf50
}
80%,90%{stroke:#ff9800
}
}
@keyframes ui-progress-circular-color{
0%,100%{stroke:#f44336
}
40%{stroke:#2196f3
}
66%{stroke:#4caf50
}
80%,90%{stroke:#ff9800
}
}



