
.ui-alert{display:-webkit-box;display:-ms-flexbox;display:flex;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:.9375rem;line-height:1.4;margin-bottom:1rem;overflow:hidden;position:relative;-webkit-transition:margin-bottom 0.3s;transition:margin-bottom 0.3s;width:100%
}
.ui-alert a{text-decoration:none
}
.ui-alert a:hover,.ui-alert a:focus{text-decoration:underline
}
.ui-alert__icon{-ms-flex-item-align:start;align-self:flex-start;-ms-flex-negative:0;flex-shrink:0;margin-right:.75rem
}
.ui-alert__body{-webkit-box-align:center;-ms-flex-align:center;align-items:center;color:rgba(0,0,0,0.75);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row;margin-bottom:0;margin-top:0;min-height:3rem;padding:.75rem 1rem;-webkit-transition:opacity 0.3s, margin-top 0.4s;transition:opacity 0.3s, margin-top 0.4s;width:100%
}
.ui-alert__content{-webkit-box-flex:1;-ms-flex-positive:1;flex-grow:1
}
.ui-alert__dismiss-button{-ms-flex-item-align:start;align-self:flex-start;-ms-flex-negative:0;flex-shrink:0;margin-bottom:-.25rem;margin-left:.5rem;margin-right:-.5rem;margin-top:-.25rem
}
.ui-alert--type-info .ui-alert__body{background-color:rgba(33,150,243,0.12)
}
.ui-alert--type-info .ui-alert__icon{color:#2196f3
}
.ui-alert--type-info a{color:#2196f3
}
.ui-alert--type-success .ui-alert__body{background-color:rgba(76,175,80,0.12)
}
.ui-alert--type-success .ui-alert__icon{color:#4caf50
}
.ui-alert--type-success a{color:#4caf50
}
.ui-alert--type-warning .ui-alert__body{background-color:rgba(255,152,0,0.12)
}
.ui-alert--type-warning .ui-alert__icon{color:#ff9800
}
.ui-alert--type-warning a{color:#ff9800
}
.ui-alert--type-error .ui-alert__body{background-color:rgba(244,67,54,0.12)
}
.ui-alert--type-error .ui-alert__icon{color:#f44336
}
.ui-alert--type-error a{color:#f44336
}
.ui-alert--transition-toggle-enter,.ui-alert--transition-toggle-leave-active{margin-bottom:0
}
.ui-alert--transition-toggle-enter .ui-alert__body,.ui-alert--transition-toggle-leave-active .ui-alert__body{margin-top:-3.5rem;opacity:0
}



