
.dropout-info .modal .body .main-view .legend text{alignment-baseline:middle;font-family:sans-serif;font-size:12px;fill:black
}
.dropout-info .modal .body .main-view .cluster text{text-anchor:middle
}
.dropout-info .modal .body .main-view .main-plot .stem{stroke-width:2;stroke:#888888;opacity:0.8
}
.dropout-info .modal .body .main-view .main-plot .empty-circle{r:5px;fill:#888888
}
.dropout-info .modal .body .main-view .main-plot .inner-circle{opacity:1
}
.dropout-info .modal .body .main-view .main-plot .vertical-seperator{fill:none;stroke-width:0.5px;stroke:#000000
}
.dropout-info .modal .body .main-view .main-plot .horizontal-bar{fill:#238b45;opacity:0.2;stroke-width:1;stroke:#000000
}
.dropout-info .modal .body .main-view .axis path,.dropout-info .modal .body .main-view .axis line{fill:none;stroke:#000000;stroke-width:2px
}



