
.ui-slider{-webkit-box-align:center;-ms-flex-align:center;align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex;outline:none
}
.ui-slider:not(.is-disabled).is-active:not(.has-marker) .ui-slider__thumb::before,.ui-slider:not(.is-disabled).is-dragging:not(.has-marker) .ui-slider__thumb::before{-webkit-transform:scale(1);transform:scale(1)
}
.ui-slider:not(.is-disabled).is-active .ui-slider__marker,.ui-slider:not(.is-disabled).is-dragging .ui-slider__marker{opacity:1;-webkit-transform:scale(1) translateY(-1.625rem);transform:scale(1) translateY(-1.625rem)
}
.ui-slider:not(.is-disabled).is-active .ui-slider__marker-text,.ui-slider:not(.is-disabled).is-dragging .ui-slider__marker-text{color:white
}
.ui-slider:not(.is-disabled).is-active .ui-slider__snap-point,.ui-slider:not(.is-disabled).is-dragging .ui-slider__snap-point{opacity:1
}
.ui-slider:not(.is-disabled).is-active .ui-slider__icon .ui-icon,.ui-slider:not(.is-disabled).is-dragging .ui-slider__icon .ui-icon{color:#2196f3
}
.ui-slider.is-disabled .ui-slider__icon{opacity:0.6
}
.ui-slider.is-disabled .ui-slider__track{cursor:default
}
.ui-slider.is-disabled .ui-slider__track-fill{background-color:transparent
}
.ui-slider.is-disabled .ui-slider__thumb{background-color:#DDD;border:.125rem solid white
}
.ui-slider__icon{margin-right:1rem
}
.ui-slider__icon .ui-icon{color:rgba(0,0,0,0.54);-webkit-transition:color .2s ease;transition:color .2s ease
}
.ui-slider__track{-webkit-box-align:center;-ms-flex-align:center;align-items:center;cursor:pointer;display:-webkit-box;display:-ms-flexbox;display:flex;height:1.125rem;margin:0 auto;position:relative;width:100%
}
.ui-slider__track-background,.ui-slider__track-fill{content:'';display:block;height:.1875rem;left:0;position:absolute;top:.46875rem
}
.ui-slider__track-background{background-color:rgba(0,0,0,0.12);width:100%
}
.ui-slider__snap-point{background-color:rgba(0,0,0,0.75);height:.1875rem;opacity:0;position:absolute;-webkit-transform:opacity 0.2s ease;transform:opacity 0.2s ease;width:.125rem;z-index:1
}
.ui-slider__track-fill{background-color:#2196f3;-webkit-transform-origin:left;transform-origin:left;width:100%
}
.ui-slider__thumb{background-color:#2196f3;border-radius:50%;cursor:inherit;display:block;height:.875rem;left:0;position:relative;width:.875rem;z-index:1
}
.ui-slider__thumb::before{background-color:rgba(33,150,243,0.38);border-radius:50%;content:'';display:block;height:2.25rem;margin-left:-.6875rem;margin-top:-.6875rem;position:absolute;-webkit-transform-origin:center;transform-origin:center;-webkit-transform:scale(0);transform:scale(0);-webkit-transition:-webkit-transform .2s ease;transition:-webkit-transform .2s ease;transition:transform .2s ease;transition:transform .2s ease, -webkit-transform .2s ease;width:2.25rem
}
.ui-slider__marker{height:2.25rem;margin-left:-.6875rem;margin-top:-.6875rem;opacity:0;position:absolute;-webkit-transform:scale(0) translateY(0);transform:scale(0) translateY(0);-webkit-transition:all .2s ease;transition:all .2s ease;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:2.25rem
}
.ui-slider__marker svg{fill:#2196f3;height:2.25rem;width:2.25rem
}
.ui-slider__marker-text{color:#2196f3;font-size:.8125rem;font-weight:500;left:0;position:absolute;text-align:center;top:.25rem;-webkit-transition:color .2s ease;transition:color .2s ease;width:2.25rem
}
.ui-slider--is-dragging{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}



