
.ui-radio{-webkit-box-align:center;-ms-flex-align:center;align-items:center;display:-webkit-box;display:-ms-flexbox;display:flex;font-family:Roboto,-apple-system,BlinkMacSystemFont,"Segoe UI",Oxygen,Ubuntu,Cantarell,"Fira Sans","Droid Sans",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";font-size:.9375rem;height:1.25rem;margin:0
}
.ui-radio:hover:not(.is-disabled):not(.is-checked) .ui-radio__outer-circle{border:.125rem solid rgba(0,0,0,0.54)
}
.ui-radio.is-checked .ui-radio__inner-circle{opacity:1;-webkit-transform:scale(0.5);transform:scale(0.5);z-index:0
}
.ui-radio.is-disabled{opacity:0.5
}
.ui-radio.is-disabled .ui-radio__input-wrapper,.ui-radio.is-disabled .ui-radio__label-text{cursor:default
}
.ui-radio__input-wrapper{cursor:pointer;height:1.25rem;position:relative;width:1.25rem
}
.ui-radio__input{-webkit-appearance:none;-moz-appearance:none;appearance:none;height:1px;left:0;margin:0;opacity:0;outline:none;padding:0;position:absolute;top:0;width:1px
}
body[modality="keyboard"] .ui-radio__input:focus+.ui-radio__focus-ring{opacity:1;-webkit-transform:scale(1);transform:scale(1)
}
.ui-radio__outer-circle{background-color:transparent;border-radius:50%;border:.125rem solid rgba(0,0,0,0.38);height:1.25rem;left:0;position:absolute;top:0;-webkit-transition:border-color 0.2s;transition:border-color 0.2s;width:1.25rem
}
.ui-radio__inner-circle{background-color:rgba(0,0,0,0.38);border-radius:50%;height:1.25rem;left:0;opacity:0;position:absolute;top:0;-webkit-transform:scale(1.2);transform:scale(1.2);-webkit-transition-duration:.3s;transition-duration:.3s;-webkit-transition-property:opacity, background-color, -webkit-transform;transition-property:opacity, background-color, -webkit-transform;transition-property:transform, opacity, background-color;transition-property:transform, opacity, background-color, -webkit-transform;width:1.25rem;z-index:-1
}
.ui-radio__focus-ring{background-color:rgba(0,0,0,0.1);border-radius:50%;height:2.625rem;left:-.6875rem;opacity:0;position:absolute;top:-.6875rem;-webkit-transform:scale(0);transform:scale(0);-webkit-transition:background-color 0.2s ease, opacity 0.15s ease, -webkit-transform 0.15s ease;transition:background-color 0.2s ease, opacity 0.15s ease, -webkit-transform 0.15s ease;transition:background-color 0.2s ease, transform 0.15s ease, opacity 0.15s ease;transition:background-color 0.2s ease, transform 0.15s ease, opacity 0.15s ease, -webkit-transform 0.15s ease;width:2.625rem;z-index:-1
}
.ui-radio__label-text{cursor:pointer;font-size:1rem;margin-left:.5rem
}
.ui-radio--button-position-right .ui-radio__label-text{margin-left:0;margin-right:auto;-webkit-box-ordinal-group:0;-ms-flex-order:-1;order:-1
}
.ui-radio--color-primary.is-checked:not(.is-disabled) .ui-radio__outer-circle{border-color:#2196f3
}
.ui-radio--color-primary.is-checked:not(.is-disabled) .ui-radio__inner-circle{background-color:#2196f3
}
.ui-radio--color-primary.is-checked:not(.is-disabled) .ui-radio__focus-ring{background-color:rgba(33,150,243,0.2)
}
.ui-radio--color-accent.is-checked:not(.is-disabled) .ui-radio__outer-circle{border-color:#d500f9
}
.ui-radio--color-accent.is-checked:not(.is-disabled) .ui-radio__inner-circle{background-color:#d500f9
}
.ui-radio--color-accent.is-checked:not(.is-disabled) .ui-radio__focus-ring{background-color:rgba(213,0,249,0.2)
}



