
.main-page{position:relative;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;height:100vh;width:100%
}
.main-page>.header{font-size:1.5rem;height:3.875rem;background-color:#000;-ms-flex-negative:0;flex-shrink:0
}
.main-page>.page{height:100%;width:100%
}
.main-page>.page>.content{position:relative;overflow-y:scroll
}
.main-page>.page>.content>.video-part{padding-left:40px;padding-right:20px
}
.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-current-time,.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-time-divider,.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-duration{display:block
}
.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-progress-control{position:absolute;bottom:2.5em;left:0;right:0;width:100%;height:10px
}
.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-progress-holder{margin-left:0px;margin-right:0px
}
.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-fullscreen-control{position:absolute;bottom:0;right:0
}
.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-playback-rate{position:absolute;bottom:0;right:28px
}
.main-page>.page>.content>.video-part>.video-player .vjs-control-bar>.vjs-volume-menu-button{position:absolute;bottom:0;right:55px
}
.main-page>.page>.content>.chart-part{position:relative;padding-left:40px;padding-right:20px
}
.main-page>.page>.content>.chart-part>#content-video-progress-bar{position:absolute;top:0;width:0;height:100%;background:#aaaaaa;border-right:dashed 1px;border-color:#000000;opacity:0.2;z-index:-3
}
#stacked-area-graph{width:calc(100% + 40px);margin-left:-40px
}



